import {commumal,listWindow,descroll} from "../public";
var auditedMore={
    methods:{
        init(){
            $(".chooseCondition ul li i").on("click",listWindow.defaultDelete);
            $(".weui-btn.query").on("click",auditedMore.events.openWindow);
            $(".content").scroll(auditedMore.events.scroll);
            $(".btnBack").on("click",commumal.back)
        }
    },
    events:{
        condition(data){
            let html='';
            for(let i=0;i<data.length;i++){
                 html+=`
                    <button data-value='${data[i].OA_TYPE}'>${data[i].OA_TYPE_NAME}</button>
                 `
            }
            let $html=$(html);
            $html.eq(0).addClass("select");
            $html.on("click",auditedMore.events.selectOption)
            $(".condition .buttons").html($html)
        },
        selectOption(){
            let that=$(this);
            that.addClass("select").siblings().removeClass("select");
            listWindow.backToStartPoint();
            auditedMore.events.fetchCache({refresh:true,complete:false})
        },
        openWindow(){
            listWindow.backToStartPoint();
            listWindow.query({
                cache:"MoreDate",
                loadCondition:(data)=>{
                    auditedMore.events.loadCondition(data)
                   
                },
                fresh:(data)=>{
                    auditedMore.events.fetchData(data,{refresh:true,complete:false})
                }
            })
        },
        loadCondition(data){
            let html="";
            for(let i in data){
               html+=`
                 <li value="${data[i].value.index}">
                     <span>${data[i].name}</span>
                     <i class="iconfont icon-x"></i>
                 </li>
               `
            }
           let $html=$(html);
            $html.find("i").on("click",auditedMore.events.deleteli)
            $(".chooseCondition ul").html($html)
        },
        deleteli(){
            listWindow.backToStartPoint();
            let $li=$(this).closest("li");
            let index=$li.attr("value");
            let arr=JSON.parse(commumal.caching("MoreDate"));
            for(let i=0;i<arr.length;i++){
                if(arr[i].value.index==index){
                    arr.splice(i,1)
                }
            }
            /* 刷新 */
            auditedMore.events.fetchData(arr,{refresh:true,complete:false})
            localStorage.setItem("MoreDate",JSON.stringify(arr))
            $li.remove()
         },
         fetchData(data,ref){
             let  submitData={};
             for(let i in data){
                if(data[i].value.index==0){
                   submitData.QUERY=data[i].value.value   //取input框里面的内容
                }else{
                    let time=data[i].value.value;
                    submitData.DATE_FRM=time.start;
                    submitData.DATE_TO=time.end;   //取开始结束时间
                }
            }
            if(ref.refresh){
                descroll.config.page.index=1;
            }
            submitData.Type=$(".buttons .select").attr("data-value");
            let date=moment().format('YYYY-MM-DD');
            let endData={
                 m :"LIST",
                 REVIEW_IND:"N",
                 DATE_FRM:submitData.DATE_FRM||date,
                 DATE_TO:submitData.DATE_TO||date,
                 QUERY:submitData.QUERY||"",
                 page:descroll.config.page.index++,
                 rows:descroll.config.page.rows
             }
             if(submitData.Type=="ATDC"){
                 endData.SP_NAME="SP_ATDC_ATTENDANCE_INFO_LIST"
             }else if(submitData.Type=="RBRB"){
                 endData.SP_NAME="SP_RBRB_REIMBURSE_INFO_LIST"
             }else if(submitData.Type=="LVLV"){
                 endData.SP_NAME="SP_LVLV_LEAVE_INFO_LIST"
             }else if(submitData.Type=="OTBL"){
                endData.SP_NAME="SP_OTBL_OVERTIME_BILL_INFO_LIST"
             }
             // console.log("提交的数据:",endData);
              auditedMore.events.fresh(endData,ref)
         },
         fetchCache(ref){
            let arr=JSON.parse(commumal.caching("MoreDate"));
            let time=moment().format('YYYY-MM-DD');
            if(!arr){
                let value=[{name:"今天",value:{index:1,value:{start:time,end:time}}}]
                auditedMore.events.fetchData(value,ref);
                $(".condition ul li").attr("value",1)
            }else{
                auditedMore.events.loadCondition(arr);
                auditedMore.events.fetchData(arr,ref)
            }
        },
        fresh(subData,ref){
            let ky=$(".buttons .select").attr("data-value");
           $.defs.myPost(subData).then((data)=>{
                  let arrangeData=auditedMore.events.arrange(data.rows,ky);
                  let html=auditedMore.events.content(arrangeData,ky);
                  let $html=$(html);
                  $html.find(".through").on("click",auditedMore.events.through);
                  $html.on("click",auditedMore.events.matterItem)
                  descroll.config.pages(data);
                  if(ref.complete){
                    if(data.rows.length!=0){
                        $(".content").append($html);
                        descroll.config.isLoad=true;
                    }else{
                        $(".content").empty()
                    }
                  }else{
                    if(data.rows.length!=0){
                        $(".content").html($html)
                    }else{
                        $(".content").empty()
                    }
                  }
           })
        },
        arrange(data,ky){
            if(ky=="ATDC"){
                data.forEach((item)=>{
                     item.ApplyDate=commumal.dateToday(item.ATDC_APPLY_DT);
                })
            }else if(ky=="LVLV"){
                data.forEach((item)=>{
                    item.ApplyDate=commumal.dateToday(item.LVLV_ENTRY_DT);
                    item.toDay=Math.ceil(item.LVLV_HOUR/4)/2;
                })
            }else if(ky=="OTBL"){
                data.forEach((item)=>{
                    item.ApplyDate=commumal.dateToday(item.OTBL_APPLY_DT)
                })
            }
            return data
        },
        content(data,ky){
            let html='';
           // console.log("content:",data)
            data.forEach((item)=>{
                 if(ky=="ATDC"){
                     html+=`
                     <div class="weui-form-preview item" index="${item.ATDC_KY}" sts="${item.SYSV_ATDC_TYPE}" review="${item.REVIEW_IND}" name="${item.MEME_NAME}">
                      <div class="weui-form-preview__hd">
                       <div class="weui-form-preview__item">
                          <label class="weui-form-preview__label black f12">${item.ATDC_ID}<span class="ml15rem">${item.SYSV_ATDC_TYPE_DESC}</span></label>
                          <em class="weui-form-preview__value black">${item.ATDC_STS_DESC}</em>
                       </div>
                      </div>
                <div class="weui-form-preview__bd">
                      <div class="weui-form-preview__item">
                          <label class="weui-form-preview__label black f12">${item.MEME_NAME}</label>
                          <span class="weui-form-preview__value black f12">${item.ApplyDate}</span>
                      </div>
                      <div class="weui-form-preview__item">
                          ${auditedMore.events.businessOrReport(item)}
                      </div>
                      <div class="weui-form-preview__item">
                          <label class="weui-form-preview__label black f12">
                              地点:${item.ATDC_SITE}
                          </label>
                      </div>
                  </div>
                  <div class="weui-form-preview__ft">${auditedMore.events.reportBtn(item)}</div>
                     </div>
                     `
                 }else if(ky=="RBRB"){
                     html+=`
                     <div class="weui-form-preview item" rbrbKey="${item.RBRB_KEY_STRING}" review="${item.REVIEW_IND}" date="${item.RBRB_APPLY_DT}" sts="${item.RBRB_STS}" dpps="${item.DPPS_KY}" name="${item.MEME_NAME}" fepp="${item.RBRB_APPLY_AMT}">
                      <div class="weui-form-preview__hd">
                        <label class="weui-form-preview__label black">${item.MEME_NAME}</label>
                        <span class="weui-form-preview__value">￥${item.RBRB_APPLY_AMT}</span>
                      </div>
                  <div class="weui-form-preview__bd">
                      <div class="weui-form-preview__item">
                          <label class="weui-form-preview__label black">发生日期:${item.RBRB_APPLY_DT}</label>
                          <span class="weui-form-preview__value black">${item.RBRB_STS_DESC}</span>
                      </div>
                  </div>
                  <div class="weui-form-preview__ft">${auditedMore.events.reimburseBtn(item)}</div>
                     </div>
                     `
                 }else if(ky=="LVLV"){
                     html+=`
                     <div class="weui-form-preview item" index="${item.LVLV_KY}" lvlvId="${item.LVLV_ID}" type="${item.SYSV_LVLV_TYPE_DESC}" reviewInd="${item.REVIEW_IND}">
                      <div class="weui-form-preview__hd">
                        <div class="weui-form-preview__item">
                          <label class="weui-form-preview__label black f12">${item.LVLV_ID}</label>
                          <em class="weui-form-preview__value">${item.LVLV_STS_DESC}</em>
                        </div>
                      </div>
                 <div class="weui-form-preview__bd f12">
                      <div class="weui-form-preview__item">
                          <label class="weui-form-preview__label black">${item.MEME_NAME}</label>
                          <span class="weui-form-preview__value black">休假时长:${item.LVLV_HOUR}小时(共${item.toDay}天)</span>
                      </div>
                      <div class="weui-form-preview__item">
                          <label class="weui-form-preview__label black">休假时间</label>
                          <span class="weui-form-preview__value black f12">${item.LVLV_FRM_DTM}至${item.LVLV_TO_DTM}</span>
                      </div>
                      <div class="weui-form-preview__item">
                          <label class="weui-form-preview__label black">${item.SYSV_LVLV_TYPE_DESC}</label>
                          <span class="weui-form-preview__value black f12">申请时间:${item.ApplyDate}</span>
                      </div>
                 </div>
                 <div class="weui-form-preview__ft">${auditedMore.events.reportBtn(item)}</div>
                     </div>
                     `
                 }else if(ky=="OTBL"){
                     html+=`
                     <div class="weui-form-preview item" index="${item.OTBL_KY}" lvlvId="${item.OTBL_ID}" reviewInd="${item.REVIEW_IND}">
                      <div class="weui-form-preview__hd">
                        <div class="weui-form-preview__item">
                          <label class="weui-form-preview__label black f12">${item.OTBL_ID}</label>
                          <em class="weui-form-preview__value">${item.OTBL_STS_DESC}</em>
                        </div>
                      </div>
                  <div class="weui-form-preview__bd f12">
                      <div class="weui-form-preview__item">
                          <label class="weui-form-preview__label black">${item.MEME_NAME}</label>
                          <span class="weui-form-preview__value black">加班时长:${item.OTBL_HOUR}小时</span>
                      </div>
                      <div class="weui-form-preview__item">
                          <label class="weui-form-preview__label black">加班时间</label>
                          <span class="weui-form-preview__value black f12">${item.OTBL_START_DT}至${item.OTBL_END_DT}</span>
                      </div>
                      <div class="weui-form-preview__item">
                          <label class="weui-form-preview__label black">申请时间</label>
                          <span class="weui-form-preview__value black f12">${item.ApplyDate}</span>
                      </div>
                  </div>
                  <div class="weui-form-preview__ft">${auditedMore.events.overTimeBtn(item)}</div>
                     </div>
                     `
                 }
            })
            return html
        },
        businessOrReport(data){
            let html='';
            if(data.SYSV_ATDC_TYPE=="BSTP"){
                html=` <label class="weui-form-preview__label black f12">发生日期:${data.ATDC_START_DT}起${data.ATDC_DAY}天<span class="ml15rem">${data.ATDC_HUMAN_CNT}人</span></label>`
            }else if(data.SYSV_ATDC_TYPE=="ETTN"){
                html=`<label class="weui-form-preview__label black f12">发生日期:${data.ATDC_START_DT}<span class="ml15rem">${data.ATDC_HUMAN_CNT}人</span></label>`
            }
            return html
       },
       reportBtn(data){
        let html='';
        if(data.REVIEW_IND=="Y"&&data.AUDIT_BUTTON_FEATURE=="AUDIT"){
            html=`<a class="weui-form-preview__btn weui-form-preview__btn_primary through" href="javascript:">通过</a>`
        }else if(data.REVIEW_IND=="Y"&&data.AUDIT_BUTTON_FEATURE=="AGREE"){
            html=`<a class="weui-form-preview__btn weui-form-preview__btn_primary through" href="javascript:">已知晓</a>`
        }
         return html
      },
      reimburseBtn(data){
        let html='';
        if(data.REVIEW_IND == "Y"){
            html=`<button type="submit" class="weui-form-preview__btn through" href="javascript:">通过</button>`
        }
        return html
      },
      overTimeBtn(data){
        let html='';
        if(data.REVIEW_IND=="Y"&&data.AUDIT_FEATURE=="AUDIT"){
            html=`<a class="weui-form-preview__btn weui-form-preview__btn_primary through" href="javascript:">通过</a>`
        }else if(data.REVIEW_IND=="Y"&&data.AUDIT_FEATURE=="AGREE"){
            html=`<a class="weui-form-preview__btn weui-form-preview__btn_primary through" href="javascript:">已知晓</a>`
        }
        return html;
    },
      scroll(){
        var that=this;
        descroll.events.onscroll(that,{
            datafresh:function () {
                   auditedMore.events.fetchCache({refresh:false,complete:true})
            }
        })
      },
      through(ev){
        ev.stopPropagation();
        ev.preventDefault();
        var ky=$(".buttons .select").attr("data-value");
        var $item=$(this).closest(".weui-form-preview");
        if(ky=="ATDC"){
            let atdcKy=$item.attr("index");
            let name=$item.attr("name");
            let typeName=$item.find("div.weui-form-preview__hd label span").text();
           commumal.confirm({
               title:'是否确定通过'+name+"的"+typeName+"申请？",
                click:()=>{
                    auditedMore.events.reportThrough(atdcKy)
                }
           })
            
        }else if(ky=="RBRB"){
             let  atdcKy=$item.attr("rbrbKey");
             let name=$item.attr("name");
             let feppName=$item.attr("fepp");
             commumal.confirm({
                 title:"是否确定通过"+name+"的"+feppName+"元的费用申请？",
                 click:()=>{
                     auditedMore.events.reimburseThrough(atdcKy)
                 }
             })
        }else if(ky=="LVLV"){
            let lvlvKy=$item.attr("index");
            let lvlvId=$item.attr("lvlvId");
            let type=$item.attr("type");
            commumal.confirm({
                 title:'确定通过单号为'+lvlvId+"的"+type+"申请？",
                 click:()=>{
                     auditedMore.events.askforleaveThrough(lvlvKy)
                 }
            })
        }else if(ky=="OTBL"){
            let  otblKy=$item.attr("index");
            let otblId=$item.attr("lvlvid");
            commumal.confirm({
                 title:'确定通过单号为'+otblId+"的加班申请？",
                 click:()=>{
                     auditedMore.events.overtimeThrough(otblKy)
                 }
            })
        }
      },
      reportThrough(atdcKy){
          $.defs.loadingshow();
          $.defs.myPost({
            m:"UPDATE",
            SP_NAME:"SP_ATDC_ATTENDANCE_INFO_STATUS_UPDATE",
            REVIEW_IND:"Y",
            ATDC_KY:atdcKy
          }).then((data)=>{
            $.defs.loadinghide()
            if(data.RETURN_CODE==0){
                weui.toast('操作成功', 2500);
                auditedMore.events.fetchCache({refresh:true,complete:false})
            }else{
                $.defs.msg(data.RETURN_MESSAGE)
            }
          })
      },
      reimburseThrough(atdcKy){
           let arr=atdcKy.split(",");
           let array=[];
           arr.forEach((item)=>{
               array.push({
                  SP_NAME:"SP_RBRB_REIMBURSE_INFO_STATUS_UPDATE",
                  RBRB_KY:item,
                  REVIEW_IND:"Y"
               })
           })
           $.defs.loadingshow();
           $.defs.myPost({
               m:"batch_update_transation",
               TB_NAME:"teturnind",
               DETAIL_STRING: JSON.stringify(array)
           }).then((data)=>{
              $.defs.loadinghide();
               if(data.RETURN_CODE==0){
                  weui.toast('操作成功', 2500);
                  auditedMore.events.fetchCache({refresh:true,complete:false})
               }else{
                $.defs.msg(data.RETURN_MESSAGE)
               }
           })
      },
      askforleaveThrough(lvlvKy){
          $.defs.loadingshow();
          $.defs.myPost({
            m:"UPDATE",
            SP_NAME:"SP_LVLV_LEAVE_INFO_STATUS_UPDATE",
            REVIEW_IND:"Y",
            LVLV_KY:lvlvKy
          }).then((data)=>{
             $.defs.loadinghide();
             if(data.RETURN_CODE==0){
                 weui.toast('操作成功', 2500);
                 auditedMore.events.fetchCache({refresh:true,complete:false})
             }else{
                $.defs.msg(data.RETURN_MESSAGE)
             }
          })
      },
      overtimeThrough(otblKy){
         $.defs.loadingshow();
         $.defs.myPost({
            m:"UPDATE",
            SP_NAME:"SP_OTBL_OVERTIME_BILL_INFO_STATUS_UPDATE",
            REVIEW_IND:"Y",
            OTBL_KY:otblKy
         }).then((data)=>{
            $.defs.loadinghide()
            if(data.RETURN_CODE==0){
                weui.toast('操作成功', 2500);
                auditedMore.events.fetchCache({refresh:true,complete:false})
            }else{
                $.defs.msg(data.RETURN_MESSAGE)
            }
         })
      },
      matterItem(){
        var ky=$(".buttons .select").attr("data-value");
        var $item=$(this).closest(".weui-form-preview");
        localStorage.setItem("auditMore",ky);//记住离开时的位置
        if(ky=="ATDC"){
            let atdcKy=$item.attr("index");
            let review=$item.attr("review");
            if(review=="Y"){
                spspa.setMessage("auditing",{
                    content:{
                        atcdKy:atdcKy,
                    }
                })
                spspa.go("/records")
            }else{
                window.location.href="../index/report#/myQuery?atdcky="+atdcKy;
            }
        }else if(ky=="RBRB"){
            let review=$item.attr("review");
            let date=$item.attr("date");
            let rbrbsts=$item.attr("sts");
            let dpps=$item.attr("dpps");
            let rbrbky=$item.attr("rbrbkey")
            if(review=="Y"){
               spspa.setMessage("auditing",{
                   content:{
                    DATE:date,
                    RBRB_STS:rbrbsts,
                    APPLY_EHUSER:dpps
                   }
               })
               spspa.go("/apply")
            }else{
                window.location.href="../index/reimburse#/myLookup?RBRB_STS="+rbrbsts+"&APPLY_EHUSER="+dpps+"&DATE="+date
            }
        }else if(ky=="LVLV"){
            let ivivKey=$item.attr("index");
            let review=$item.attr("reviewInd");
            if(review=="Y"){
                spspa.setMessage("auditing",{
                    content:{
                        lvlvKy:ivivKey,
                    }
                })
                spspa.go("/leave")
            }else{
                window.location.href="../index/askForLeave#/myQuery?lvlv="+ivivKey
            }
        }else if(ky=="OTBL"){
             let otblKy=$item.attr("index");
             let review=$item.attr("reviewInd");
             if(review=="Y"){
                 spspa.setMessage("auditing",{
                     content:{
                        otblKy:otblKy
                     }
                 })
                 spspa.go("/work")
             }else{
                 window.location.href="../index/overTime#/myQuery?otbl="+otblKy
             }
        }

      }


    },
    config:{
        init(){
            $(".top .huan").text("待审核>查看更多")
            let arr=JSON.parse(commumal.caching("BASE_INFO"));
            auditedMore.events.condition(arr.oa_type);
            let itemPosition=commumal.caching("auditMore");
            if(itemPosition){
                $(".buttons button[data-value="+itemPosition+"]").addClass("select").siblings().removeClass("select")
            }
            auditedMore.events.fetchCache({refresh:true,complete:false})
        }
    }
}
$(function(){
    auditedMore.config.init();
    auditedMore.methods.init();
})


